import React, { useState } from "react";
import { Button, Modal } from "react-bootstrap";
import { Link } from "react-router-dom";

const Header = () => {
  const [signin, setSignin] = useState(false);
  const [signup, setSignup] = useState(false);
  const handleCloseSignin = () => setSignin(false);
  const handleShowSignin = () => setSignin(true);
  const handleCloseSignup = () => setSignup(false);
  const handleShowSignup = () => setSignup(true);

  return (
    <div className="header-main-container">
      <ul className="header-container">
        <li className="list-item">
          <Link to="/" className="header-item">
            Home /
          </Link>
        </li>
        <li className="list-item">
          <Link to="/mysite" className="header-item">
            MySite /
          </Link>
        </li>
        <li className="list-item">
          <Link to="/addpost" className="header-item">
            NewPost /
          </Link>
        </li>
      </ul>
      <div className="button-container">
        <Button className="signInBtn" onClick={handleShowSignin}>
          sign in
        </Button>
        <Button className="signInBtn" onClick={handleShowSignup}>
          sign up
        </Button>
      </div>
      <Modal
        backdrop="static"
        keyboard={false}
        show={signin}
        onHide={handleCloseSignin}
      >
        <Modal.Header closeButton>
          <Modal.Title>Sign In</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseSignin}>
            Close
          </Button>
          <Button variant="primary" onClick={handleCloseSignin}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
      <Modal
        backdrop="static"
        keyboard={false}
        show={signup}
        onHide={handleCloseSignup}
      >
        <Modal.Header closeButton>
          <Modal.Title>Sign Up</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseSignup}>
            Close
          </Button>
          <Button variant="primary" onClick={handleCloseSignup}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default Header;
